import Vue from 'vue'
import Router from 'vue-router'
import PostList from './views/PostList'
import Article from './views/Article'
import User from './views/User'
import Login from './views/Login'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'postList',
      components: {
        main: PostList
      }
    },
    {
      path: '/topic/:id',
      name: 'article',
      components: {
        main: Article
      }
    },
    {
      path: '/user/:loginname',
      name: 'user',
      components: {
        main: User
      }
    },
    {
      path: '/login',
      name: 'login',
      components: {
        main: Login
      }
    },
    {
      path: '/topic',
      redirect: '/'
    },
    {
      path: '/user',
      redirect: '/'
    }
  ]
})
