<template>
  <div class="topic">
    <div class="left">
      <div class="title" v-html='article.title'></div>
      <div class="info">
        <span>
          发布于&nbsp;
          {{article.create_at|formatDate()}}
          &nbsp;•&nbsp;
        </span>
        作者：
        <router-link :to="{name: 'user', params: {loginname: article.loginname}}">
          {{article.loginname}}
        </router-link>
        &nbsp;•&nbsp;
        <span>{{article.visit_count}}次浏览&nbsp;•&nbsp;</span>
        <span>
          来自：{{tabs[article.tab] && tabs[article.tab].name}}
        </span>
      </div>
      <Divider/>
      <div class="content" v-html='article.content'></div>
      <Reply :replyData="article.replies"/>
    </div>
    <div class="right">
      <ProfilePanel :loginName='article.loginname'/>
      <RecentCreate :titleNum="titleNum"/>
      <RecentJoin :titleNum="titleNum"/>
    </div>
  </div>
</template>

<script>
import Divider from '@/components/Divider'
import Reply from '@/views/Reply'
import ProfilePanel from '@/views/ProfilePanel'
import RecentCreate from '@/views/RecentCreate'
import RecentJoin from '@/views/RecentJoin'

export default {
  name: 'Article',
  components: {
    Divider,
    Reply,
    ProfilePanel,
    RecentCreate,
    RecentJoin
  },
  data () {
    return {
      isLoading: false,
      article: {},
      titleNum: 8,
      tabs: {
        all: {
          name: '全部',
          type: 'all'
        },
        good: {
          name: '精华',
          type: 'good'
        },
        share: {
          name: '分享',
          type: 'share'
        },
        ask: {
          name: '问答',
          type: 'ask'
        },
        job: {
          name: '招聘',
          type: 'job'
        }
      }
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.isLoading = true
      this.getArticlesData(this.$route.params.id)
    })
  },
  beforeRouteUpdate (to, from, next) {
    this.getArticlesData(to.params.id)
    next()
  },
  methods: {
    getArticlesData (id) {
      this.$http.fetchGet(`/topic/${id}`).then(data => {
        this.article = {
          ...data.data.data,
          ...data.data.data.author
        }
      }).catch(function () {

      })
    }
  }
}
</script>

