<template>
  <div class="user-panel">
    <ProfilePanel :loginName="loginName"/>
    <RecentCreate :title-num="topicsNum"/>
    <RecentJoin :title-num="repliesNum"/>
  </div>
</template>

<script>
import ProfilePanel from '@/views/ProfilePanel'
import RecentCreate from '@/views/RecentCreate'
import RecentJoin from '@/views/RecentJoin'

export default {
  name: 'User',
  components: {
    ProfilePanel,
    RecentCreate,
    RecentJoin
  },
  data () {
    return {
      loginName: '',
      topicsNum: 0,
      repliesNum: 0
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.loginName = this.$route.params.loginname
      this.getTitleNum(this.loginName)
    })
  },
  /**
     * 在当前路由改变，但是该组件被复用时调用
     * 举例来说，对于一个带有动态参数的路径 /user/:id，在 /user/shuiRong 和 /user/martin 之间跳转的时候，
     * 由于会渲染同样的 User 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
     * 可以访问组件实例 `this`
     */
  beforeRouteUpdate (to, from, next) {
    this.loginName = to.params.loginname
    next()
  },
  methods: {
    getTitleNum (userName) {
      this.$http.fetchGet(`/user/${userName}`).then(data => {
        let userInfo = data.data.data
        this.topicsNum = userInfo.recent_topics.length
        this.repliesNum = userInfo.recent_replies.length
      })
    }
  }
}
</script>

