<template>
  <div class="pagination">
    <el-pagination
      background
      layout="prev, pager, next"
      @current-change="handleCurrentChange"
      :page-size="pageSize"
      :total="totalNum*10">
    </el-pagination>
  </div>
</template>

<script>
export default {
  name: 'Pagination',
  props: {
    pageSize: {
      type: Number,
      required: true
    },
    totalNum: {
      type: Number,
      required: true
    }
  },
  methods: {
    handleCurrentChange (val) {
      console.log(`当前页: ${val}`)
      this.$emit('childPageNumber', `${val}`)
    }
  }
}
</script>

<style scoped>
  .pagination {
    text-align: center;
  }
</style>
