<template>
  <div class="panel" v-if="userInfo.recent_replies !== undefined && userInfo.recent_replies.length">
    <header>最近参与的话题</header>
    <Divider class="divider"/>
    <template v-for="(item, index) in userInfo.recent_replies">
      <div class="current-topic" :key="item.id" v-if="index < titleNum">
        <router-link :to="{name: 'article', params: {id: item.id}}">{{item.title}}</router-link>
      </div>
    </template>
  </div>
</template>

<script>
import Divider from '@/components/Divider'
// 事件巴士，方便兄弟组件间的数据传递
import EventBus from '@/tools/eventBus'

export default {
  name: 'RecentJoin',
  props: {
    titleNum: {
      type: Number,
      required: true
    }
  },
  components: {
    Divider
  },
  data () {
    return {
      userInfo: {}
    }
  },
  created () {
    EventBus.$off('user')
  },
  mounted () {
    this.$nextTick(() => {
      EventBus.$on('user', data => {
        this.userInfo = data
      })
    })
  }
  // computed: {
  //   // eslint-disable-next-line vue/return-in-computed-property
  //   recentTopics () {
  //     if (this.userInfo.recent_replies) {
  //       return this.userInfo.recent_replies.slice(0, 3)
  //     }
  //   }
  // }
}
</script>

<style scoped>
  header {
    font-weight: 700;
  }

  .panel {
    font-size: 14px;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
    padding: 20px;
    margin: 20px 0;
  }

  .panel .divider {
    margin: 12px 0;
  }

  a {
    color: #42b983;
    font-weight: 600;
    padding: 0 2px;
    text-decoration: none;
  }

  .panel .current-topic {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    color: #42b983;
    margin-top: 10px;
  }
</style>
