<template>
  <div class="post-list">
    <div class="loading" v-if="isLoading">
      <img src="../assets/images/loading.gif" alt="">
    </div>
    <ul class="list">
      <li>
        <div class="topBar">
          <span :class="tab === item.type ? 'current_tab':''" v-for="item in tabs" :key="item.type"
                @click="getTabData(item.type)">{{item.name}}</span>
        </div>
      </li>
      <li class="clearFix" v-for="list in listData" :key="list.id">
        <router-link :to="{name: 'user', params: {loginname: list.author.loginname}}">
          <img class="avatar" :src="list.author && list.author.avatar_url" alt="头像">
        </router-link>
        <span class="count">
          <span class="reply-count">
            <em>{{list.reply_count}}</em><em>/{{list.visit_count}}</em>
          </span>
        </span>
        <span class="label"
              :class="[{good:(list.good === true),top: (list.top === true),other_tab:(list.good !== true && list.top !== true)}]">{{list|tabFormat}}</span>
        <router-link class="title" :to="{name: 'article', params: {id: list.id}}">
          <span>{{list.title}}</span>
        </router-link>
        <span class="time">
          {{list.last_reply_at|formatDate}}
        </span>
      </li>
      <Pagination :pageSize="limit" :totalNum="this.listData.length" @childPageNumber="getCurrentPage"></Pagination>
    </ul>
  </div>
</template>

<script>
import Pagination from '@/components/Pagination'

export default {
  name: 'PostList',
  components: {
    Pagination
  },
  data () {
    return {
      isLoading: false,
      listData: [],
      limit: 20,
      page: 1,
      tab: 'all',
      tabs: {
        all: {
          name: '全部',
          type: 'all'
        },
        good: {
          name: '精华',
          type: 'good'
        },
        share: {
          name: '分享',
          type: 'share'
        },
        ask: {
          name: '问答',
          type: 'ask'
        },
        job: {
          name: '招聘',
          type: 'job'
        }
      },
      store: {}
    }
  },
  beforeMount () {
    this.isLoading = true
    this.getListData()
  },
  methods: {
    getListData () {
      let params = {
        limit: this.limit,
        page: this.page,
        tab: this.tab
      }
      this.$http.fetchGet('/topics', params).then(data => {
        this.isLoading = false
        this.listData = data.data.data
        // const store = this.store
        // // 将数据存储到对应的key下
        // store[this.tab] = {
        //   limit: this.limit,
        //   data: data.data.data
        // }
      }).catch(function () {

      })
    },
    getTabData (type) {
      this.page = 1
      this.tab = type
      this.getListData()
      this.listData = []
      // const store = this.store
      // if (!store[this.tab]) {
      //   this.isLoading = true
      //   this.limit = 20
      //   this.listData = []
      //   this.getListData()
      //   return
      // }
      // this.listData = store[this.tab].data
      // this.limit = store[this.tab].limit
    },
    getCurrentPage (currentPage) {
      this.page = currentPage
      this.getListData()
    }
  }
}
</script>

<style scoped>
  .post-list {
    width: 100%;
    max-width: 1200px;
    margin: 40px auto;
  }

  .list {
    margin: auto;
    width: 80%;
    padding: 20px 30px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  }

  .list li {
    margin: 20px 10px;
  }

  .topBar {
    height: 40px;
    margin-bottom: 20px;
  }

  .topBar span {
    position: relative;
    font-size: 16px;
    color: #80bd01;
    line-height: 40px;
    margin-right: 25px;
    cursor: pointer;
    transition: all ease-out 200ms;
  }

  .topBar span::before {
    position: absolute;
    content: "";
    left: 100%;
    top: 5px;
    width: 0;
    height: 100%;
    border-bottom: 2px solid transparent;
    transition: all ease-out 200ms;
  }

  .topBar span:not(.current_tab):hover::before {
    left: 0;
    top: 5px;
    width: 100%;
    border-bottom-color: #80bd01;
  }

  .topBar span:hover + span::before {
    left: 0;
  }

  .topBar span.current_tab {
    color: #fff;
    background-color: #80bd01;
    padding: 3px 6px;
    border-radius: 5px;
  }

  /*.topBar span:not(.current_tab):hover {*/
  /*  color: #005580;*/
  /*}*/

  a {
    display: inline-block;
    vertical-align: top;
  }

  img.avatar {
    width: 50px;
    vertical-align: middle;
    border-radius: 50%;
    transition: transform .4s ease-out;
  }

  img.avatar:hover {
    -webkit-transform: rotate(360deg) scale(1.2);
    -moz-transform: rotate(360deg) scale(1.2);
    -o-transform: rotate(360deg) scale(1.2);
    -ms-transform: rotate(360deg) scale(1.2);
    transform: rotate(360deg) scale(1.2);
  }

  span {
    line-height: 50px;
  }

  span.count {
    width: 50px;
    margin: 0 20px;
    text-align: center;
  }

  span.count em:first-child {
    font-size: 16px;
    color: purple;
  }

  span.count em:last-child {
    font-size: 14px;
    color: grey;
  }

  .title {
    width: calc(60% - 60px);
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 15px;
    font-weight: 400;
    /*color: #42b983;*/
    background-image: linear-gradient(90deg, red, orange);
    -webkit-background-clip: text;
    animation: hue 20s linear infinite;
    -webkit-text-fill-color: transparent;
  }

  @keyframes hue {
    from {
      -webkit-filter: hue-rotate(0);
    }

    to {
      -webkit-filter: hue-rotate(-1turn);
    }
  }

  span.label {
    font-size: 14px;
    display: inline-block;
    vertical-align: middle;
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    border-width: 1px;
    border-style: solid;
    border-radius: 4px;
    margin-right: 20px;
  }

  .good {
    color: #e6a23c;
    border-color: #faecd8;
    background-color: #fdf6ec;
  }

  .top {
    background-color: #f0f9eb;
    border-color: #e1f3d8;
    color: #67c23a;
  }

  .other_tab {
    background-color: #f4f4f5;
    border-color: #e9e9eb;
    color: #909399;
  }

  span.time {
    float: right;
    font-size: 14px;
    color: #666;
  }
</style>
