<template>
  <div class="panel">
    <router-link class="user" :to="{name: 'user', params: {loginname: user.loginname}}">
      <img class="user-avatar" :src="user.avatar_url" alt="头像">
      <span class="user-name">{{user.loginname}}</span>
    </router-link>
    <div class="integral">积分：{{user.score}}</div>
    <div class="github">
      Github：
      <a :href="`https://github.com/${user.githubUsername}`" target="_blank" rel="nofollow noopener noreferrer">
        {{user.githubUsername}}
      </a>
    </div>
    <div class="register-time">
      注册时间：{{user.create_at|formatDate()}}
    </div>
  </div>
</template>

<script>
// 事件巴士，方便兄弟间组件的数据传递
import EventBus from '@/tools/eventBus'

export default {
  name: 'ProfilePanel',
  props: ['loginName'],
  data () {
    return {
      user: {}
    }
  },
  mounted () {
    this.$nextTick(() => {
      if (!this.loginName) {
        return false
      }
      this.getPanelData(this.loginName)
    })
  },
  watch: {
    loginName (userName) {
      if (!userName) {
        return false
      }
      this.getPanelData(userName)
    }
  },
  methods: {
    getPanelData (userName) {
      this.$http.fetchGet(`/user/${userName}`).then(data => {
        this.user = data.data.data
        // 广播数据
        EventBus.$emit('user', this.user)
      })
    }
  }
}
</script>

<style scoped>
  a {
    color: #42b983;
    font-weight: 600;
    padding: 0 2px;
    text-decoration: none;
  }

  .panel {
    font-size: 14px;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
    padding: 20px;
  }

  .user {
    display: block;
    margin-bottom: 10px;
  }

  .user .user-avatar {
    width: 48px;
    vertical-align: middle;
    border-radius: 5px;
    margin-right: 20px;
  }

  .github {
    margin: 10px 0;
  }
</style>
